
.head {
    top: 0px;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;

    background-image: url('../background.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top; 
    height: 80vh;
}

.logo {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 50px;
    color: white;
}

.logo h1 {
    font-family: 'DSZierschrift';
    font-size: 40px;

    text-shadow: 0px 4px 4px rgba(0,0,0,0.25);
}

.logo h5 {
    font-family: 'IM Fell English';
    font-style: italic;
    font-size: 20px;

    text-shadow: 0px 4px 4px rgba(0,0,0,0.25);
}

.navbar {
    display: flex;
    justify-content: space-between;
    width: 80vw;
}

.navbar h5 {
    color: white;
    font-family: 'Poppins';
    font-weight: 700;
    font-size: 20px;

    text-shadow: 0px 4px 4px rgba(0,0,0,0.25);
}

.burger {
    display: none;
}

.menu {
    display: none;
}

@keyframes text_in {
    from {
        opacity: 0;
        transform: translateX(-100px);
    }

    to {
        opacity: 1;
    }
}

.presentation {
    width: 50vw;
    display: flex;
    position: absolute;
    top: 350px;
    left: 180px;

    animation: text_in 1s;
    animation-iteration-count: 1;
}

.presentation p {
    color: white;
    
    font-family: 'Poppins';
    font-size: 50px;
    font-weight: 700;

    text-shadow: 0px 4px 4px rgba(0,0,0,0.25);
}

@media screen and (max-width: 1200px) {
    
    .navbar {
        display: none;
    }

    .logo {
        margin-top: 70px;
    }

    .burger {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: absolute;
        top: 20px;
        left: 20px;
        cursor: pointer;
        z-index: 3;
    }

    .presentation {
        justify-content: center;
        margin-top: 20px;
        width: 100vw;
        position: static;
    }

    .presentation p {
        text-align: center;
        font-size: 30px;
        margin-left: 10px;
        margin-right: 10px;
    }

    .b-box {
        width: 30px;
        height: 3px;
        background-color: white;
        margin-bottom: 3px;

        box-shadow: 0px 4px 4px rgba(0,0,0,0.25);
    }

    .menu {
        display: flex;
        width: 60vw;
        height: 100vh;
        background-color: black;
        position: absolute;
        top: 0px;
        left: 0px;
        z-index: 2;
    }

    .navbar-menu {
        margin-left: 20px;
        margin-top: 80px;
    }

    .navbar-menu h5 {
        color: white;
        font-family: 'Poppins';
        font-weight: 700;
        font-size: 16px;
        padding-bottom: 10px;
    }

}