
section {
    display: flex;
    width: 100vw;
    justify-content: center;

    background-color: black;
}

.text-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 3;
}

.text-container p {
    width: 60%;
    color: white;

    font-family: 'Poppins';
    font-weight: 500;
    font-size: 22px;
}

.player-container {
    display: flex;
    align-items: center;
    justify-content: center;

    flex: 4;
}

@keyframes popup {
    from {
    }

    to {
        box-shadow: 10px 10px 0px rgb(48, 48, 48);
        transform: translateX(-5px) translateY(-5px);
        border-radius: 20px;
    }
}

.player {
    margin-top: 60px;
    margin-bottom: 60px;
}

.player:hover {
    animation: popup 0.8s ease;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@media screen and (max-width: 1200px) {

    section {
        display: block;
    }

    .text-container p {
        margin-top: 30px;
        width: 80%;
        text-align: center;
        font-size: 18px;
    }

    .player {
        margin-top: 30px;
        margin-bottom: 80px;
    }

    .player:hover {
        animation: none;
    }
}